import { Component, h, Prop } from '@stencil/core';
import translate from '../../../global/translations';
import { Languages } from '../../date-picker/date.types';
import { ListSeparator } from '../../list/list-item.types';
import { MenuItem } from '../../menu/menu.types';
import { ActionBarItem } from '../../action-bar/action-bar.types';

/**
 * This private & internal component renders a secondary action bar, intended to be used
 * together with `limel-text-editor` component.
 *
 * This component is responsible for:
 * 1. Visualizing the built-in actions that our text editor natively supports, such as
 * typing `@` to mention a user, or typing `#` to create a tag.
 * 1. Visualizing custom actions that a consumer wants to add to the text editor.
 * 1. Visualizing a split button that performs a primary action (such as `Send`),
 * and an optional array secondary actions (such as `Schedule send`, `Send & close ticket`, etc).
 *
 * @exampleComponent limel-example-text-editor-secondary-action-bar-basic
 *
 * @beta
 * @Private
 */
@Component({
    tag: 'limel-text-editor-secondary-action-bar',
    shadow: true,
    styleUrl: 'secondary-action-bar.scss',
})
export class TextEditorSecondaryActionBar {
    /**
     * Defines the language for translations.
     * Will translate the translatable strings on the components.
     */
    @Prop({ reflect: true })
    public language: Languages = 'en';

    /**
     * The text to show on the split button.
     * It is the primary action of the split button.
     */
    @Prop({ reflect: true })
    public splitButtonLabel?: string;

    /**
     * Optional icon for the split button.
     */
    @Prop({ reflect: true })
    public splitButtonIcon?: string;

    /**
     * A list of items and separators to show
     * for in the split button's menu.
     */
    @Prop()
    public splitButtonItems?: Array<MenuItem | ListSeparator> = [];

    /**
     * Items that are placed in the action bar.
     */
    @Prop()
    public actionBarItems: Array<ActionBarItem | ListSeparator> = [];

    public render() {
        return [this.renderLimelActionBar(), this.renderLimelSplitButton()];
    }

    private renderLimelActionBar() {
        return (
            <limel-action-bar
                accessibleLabel={translate.get('loading', this.language)}
                actions={this.actionBarItems}
            />
        );
    }

    private renderLimelSplitButton() {
        return (
            <limel-split-button
                primary={true}
                label={this.splitButtonLabel}
                icon={this.splitButtonIcon}
                items={this.splitButtonItems}
                // onClick={this.onClick}
                // onSelect={this.handleSelect}
            />
        );
    }
}
